<template>
    <div class="signin">
        <img src="@/static/images/login/logo@2x.png" alt class="logo" />
        <ul class="list">
            <li>
                <img src="@/static/images/login/login_user_icon.png" alt="" />
                <input type="text" placeholder="请输入手机号" v-model="registerForm.username"/>
            </li>
            <li>
                <img src="@/static/images/login/login_password_icon.png" alt="" />
                <input type="password" placeholder="请输入密码" v-model="registerForm.password"/>
            </li>
            <li>
                <img src="@/static/images/login/login_yaoqing_icon.png" alt />
                <input :readonly="readState" type="text" placeholder="请输入邀请码" v-model="registerForm.invite_code" />
            </li>
        </ul>
        <div class="reg_box"><button @click="registerSub">立即注册</button></div>
    </div>
</template>

<script>
    import {register} from "../../api/user";
    import { toast, Dialog } from 'vant';
    export default {
        name: "Register",
        data(){
            return {
                readState:false,
                registerForm:{
                    username:'',
                    password:'',
                    invite_code:''
                }
            }
        },
        mounted()
        {
            this.registerForm.invite_code = this.$route.query.code ? this.$route.query.code : ''
            this.readState = this.registerForm.invite_code ? true : false
        },
        methods:{
            registerSub()
            {
                let form = this.registerForm
                if (form.username == '')
                {
                    toast('请输入手机号')
                    return
                }
                var reg=/^1[3456789]\d{9}$/;
                if(!reg.test(form.username)){
                   toast('请填写有效手机号')
                    return;
                }
                if (!form.password || form.password == ''){
                    toast('请填写密码')
                    return
                }
                if (form.password.length < 6){
                    toast('密码长度最少为6位')
                    return
                }
                if (form.invite_code == '')
                {
                    toast('请输入邀请码')
                    return
                }
                register(form).then(res => {

                    if (res.error > 0)
                    {
                        toast(res.msg)
                        return
                    }

                    Dialog.alert({
                        title: '注册成功',
                        message: '请备份好您的密码，密码一旦丢失，则不可找回，账户内资产也会永久丢失，请务必妥善保存密码'
                    }).then(() => {
                        this.$router.push({path:'/login'})
                    });
                })
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/register.css";
</style>



